          .col-lg-10
            img(src='@/assets/curso/temas/t1/imag17.svg', alt='La imagen presenta un diagrama de flujo para determinar el tipo de cuerpo femenino. Comienza con la pregunta "Tus hombros y cadera tienen medida similar?" Si la respuesta es s, se pregunta "Tu cintura es ms pequea que tu cadera?", lo que identifica la forma de reloj de arena. Si la respuesta es no, se pregunta "Tus hombros miden ms que tu cadera o abdomen?", identificando la forma de tringulo invertido. Si la respuesta inicial es no, se pregunta "Tu blusa tiene una talla ms que tu pantaln?", determinando la forma ovalada si la respuesta es s, o la forma de tringulo si la respuesta es no. Cada tipo de cuerpo se ilustra con una figura femenina correspondiente.')
    
    
    
