        .bg6.p-4(data-aos="fade-down")
          p El anlisis de las extremidades anteriores y frontales, es lo que permite determinar si el caballo tiene buena postura o si por el contrario es defectuosa. Este anlisis se hace bajo especificaciones ya estipuladas las cuales permite revisar la composicin de todos los trminos anteriormente mencionados con el fin de identificar la postura.
          p.mb-2 La postura es un punto clave en el anlisis de la estructura corporal; es lo que corresponde a la distribucin de pesos frente a la gravedad. En la imagen se identifica lo que esta categorizado como postura correcta o incorrecta.

