          img.mh-290.m-auto(src='@/assets/curso/temas/t1/imag19.svg' alt='La imagen ilustra dos conceptos relacionados con la postura y el equilibrio corporal. En la seccin de la izquierda, titulada "Anlisis de diferentes posturas sobre un mismo cuerpo", se presenta cmo distintas posturas pueden alterar las medidas del cuerpo debido a hiperextensiones o flexiones. La serie de figuras presenta un mismo cuerpo en diferentes posiciones que afectan su alineacin. En la seccin de la derecha, titulada "Anlisis de punto de equilibrio", se explica que cada cuerpo tiene un tipo de equilibrio particular. Dependiendo del gnero, se identifican variaciones en el sistema seo y muscular que influyen en el punto de equilibrio. Las figuras presentan una lnea de equilibrio ideal comparada con una postura desalineada.')  
