      h2 1.2 Clasificacin de medidas
    
    p.mb-4(data-aos="fade") A continuacin, se describen los sistemas de medidas, su clasificacin, caractersticas, usos e implementacin.
    
    .subt.mb-4(data-aos="fade-right")
