        h3.mb-0.ms-2 Sistemas de medidas de longitud

    p.mb-4(data-aos="fade") La longitud es la distancia que hay entre un punto y otro. Los dos sistemas utilizados para determinar esa distancia son: sistema ingls y sistema internacional o mtrico decimal.
    p.mb-4(data-aos="fade") En el sistema ingls, la unidad bsica de medida es la pulgada, se aplica principalmente en Estados Unidos, Inglaterra y Australia. En el sistema internacional o mtrico decimal que tiene como unidad principal el metro, se usa en el resto del mundo.
    
    .cajon.color-primario.bg1.p-4.mb-5(data-aos="fade-down")
      .row.align-items-center
        .col-lg-6
          p Sin embargo, los dos tipos de sistemas son utilizados en diferentes reas y cadenas productivas teniendo en cuenta que algunos estndares estn trabajados en medidas diferentes a las que usa oficialmente el pas.
        .col-lg-3.col-md-6.mb-4.mb-md-0
          h5.text-center.text-small Sistema ingls
          img(src='@/assets/curso/temas/t1/gt.svg', alt='La imagen presenta una regla con una escala en pulgadas, que abarca desde 1 hasta 2, y sobre ella se encuentra un texto que dice "Sistema ingls" en la parte superior. La regla tiene marcas detalladas que indican las fracciones de pulgada, y el fondo de la imagen es de color lila.')
        .col-lg-3.col-md-6.mb-4.mb-md-0
          h5.text-center.text-small Sistema internacional o mtrico decimal
          img(src='@/assets/curso/temas/t1/imag21.svg', alt='La imagen presenta una regla con una escala en centmetros, que abarca desde 1 hasta 5, y sobre ella se encuentra un texto que dice "Sistema internacional o mtrico" en la parte superior. La regla tiene lneas de medicin detalladas entre cada nmero, que indican las fracciones de centmetro, y el fondo de la imagen es de color lila.')
    
    
    .row.justify-content-center 
