        h4 Sistema mtrico ingls 
        p.mb-4 Como todo nmero fraccionario, se representa A/B. Donde A es el numerador y B el denominador. El numerador indica el nmero de partes que se toman de la unidad y el denominador en cuntas partes iguales se divide esa unidad.
        p.mb-4(data-aos="fade") En confeccin, se utiliza la pulgada como medida de estandarizacin que mide el nmero de puntadas por pulgada PPP:
