    h4(data-aos="fade") Sistema mtrico decimal o universal
    p.mb-4(data-aos="fade") Es un sistema de unidades en el cual los mltiplos o submltiplos de las unidades de una misma naturaleza siguen una escala decimal (10).
    p(data-aos="fade") En confeccin: se utiliza para verificar medidas finales de la prenda. Los rangos de tolerancia de una costura se miden en milmetros.
    
    ul.lista-ul--color.fa-ul.color-secundario.ps-2.ps-md-5.mb-4(data-aos="fade")
