    p.mb-4(data-aos="fade") En patronaje: la mayora de los cuadros de tallas anatmicas e industriales, en todas las lneas, estn construidos en centmetros que es el sistema mtrico estandarizado para el proceso de toma de medidas, patronaje y corte de las prendas. Las reducciones o ampliaciones para el confort de la prenda se realizan en centmetros.
    
