    TabsC.color-primario.mb-5(data-aos="fade-down")
      .py-4.py-md-5.p-4.bg9(titulo="Sobre medidas")
        .row.justify-content-around
          .col-md-8
            p Son las medidas que se toman directamente sobre el cuerpo de la persona. Estas medidas se deben tomar teniendo en cuenta los ejes de movimiento. 
            p La cantidad de medidas a tomar depender del tipo de prenda a construir. En este caso es fundamental la observacin y anlisis de la estructura corporal para interpretar los ajustes que deben darse al trazo de patrones.
          .col-md-4.col-lg-3
            img(src='@/assets/curso/temas/t1/figuras1.svg', alt='')
      .py-4.py-md-5.p-4.bg9(titulo="Deducidas")
        .row.justify-content-around.align-items-center
          .col-md-3.col-lg-2.mb-4.mb-md-0
            img(src='@/assets/curso/temas/t1/nuev.png', alt='')
          .col-md-9
            p Se deducen o se sacan de otra medida, aplicando frmulas ya estudiadas y comprobadas con anterioridad. Se basan esencialmente en una medida horizontal, que se toma al contorno del cuerpo ms prominente y en una medida vertical que es tomada a lo largo del cuerpo de arriba hacia abajo. Con estas medidas se deducen las otras, por eso se toman exactas y no deben mezclarse con otras medidas al construir el cuadro de tallas.
      .py-4.py-md-5.p-4.bg9(titulo="Estandarizadas")
        .row.justify-content-around
          .col-md-8
            p #[b Medidas estandarizadas o normalizadas:] son el resultado de estudios antropomtricos, que establecen un rango de medidas que corresponden a las principales medidas corporales  tallas. Estas medidas son definidas y adoptada por norma. 
          .col-md-4.col-lg-3
            img(src='@/assets/curso/temas/t1/figuras3.svg', alt='')
      .py-4.py-md-5.p-4.bg9(titulo="Anatmicas")
        .row.justify-content-around
          .col-md-4.col-lg-2
            img(src='@/assets/curso/temas/t1/file.svg', alt='')
          .col-md-9
            p.my-4 Son las medidas tomadas justas sobre el cuerpo teniendo en cuenta los puntos antropomtricos y ejes de movimiento. #[b No tienen desahogos ni flojos.]
      .py-4.py-md-5.p-4.bg9(titulo="Medidas de prenda")
        .row.justify-content-around
          .col-lg-2.col-md-3.mb-4.mb-md-0
            img(src='@/assets/curso/temas/t1/figuras4.svg', alt='')
          .col-md-9
            p.mb-4 Estas incluyen medidas de holgura, teniendo en cuenta el uso de la prenda. Por lo general se construyen cuadros de tallas de prendas que ya tienen incluidos esos centmetros de ms en las diferentes medidas.
            

    h4(data-aos="fade") Medidas anatmicas, estandarizadas o normalizadas
    p.mb-4(data-aos="fade") Son el resultado de la toma de medidas directas o de estudios antropomtricos. Estas medidas se toman exactas sobre la estructura, es decir no tiene aumentos ni ajustes. De la toma correcta de las medidas depende el resultado del patrn. Estas medidas son utilizadas para la construccin y elaboracin de los bsicos, superiores e inferiores. A continuacin, puede ampliar la informacin en el siguiente video.
