    #t_1_4.titulo-segundo.color-primario(data-aos="fade-right")
      h2 1.4 Pasos del patronaje
    
    p.mb-4(data-aos="fade") El patronaje es la representacin en 2D (bidimensional) de un volumen en 3D (tridimensional), a travs de la elaboracin de planos, moldes o patrones de prendas de vestir para el cuerpo.
    
    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-lg-8
        img(src='@/assets/curso/temas/t1/caja.svg', alt='Imagen expone una explicacin visual del concepto de patronaje. A la izquierda, hay una figura 3D que representa un cubo en un sistema de coordenadas tridimensionales (ejes X, Y, Z). En el centro, se presenta el mismo cubo, pero con lneas punteadas para resaltar su forma tridimensional. A la derecha, se presenta el desarrollo plano (2D) del cubo, que consiste en seis cuadrados conectados que, cuando se pliegan, forman el cubo. El texto explica que el patronaje es la representacin en 2D (bidimensional) de un volumen en 3D (tridimensional) mediante la elaboracin de planos, moldes o patrones de prendas de vestir para el cuerpo.') 
    
    p.mb-4(data-aos="fade") El patronaje sirve para transferir la informacin de la estructura corporal en planos que luego se convierten en moldes.
    
    .row.justify-content-center 
      .col-lg-6
        img.mb-5(src='@/assets/curso/temas/t1/estructura.svg', alt='Imagen describe la relacin entre la estructura del cuerpo humano, su representacin en planos y los moldes utilizados en la confeccin de prendas. De izquierda a derecha, se observan figuras estilizadas de un hombre y una mujer etiquetadas como "Estructura". A la derecha de estas figuras, se presenta una representacin en "Plano" que descompone las formas tridimensionales en una vista bidimensional. Finalmente, se ven los "Moldes", que son las plantillas especficas para cortar las telas y confeccionar las prendas de vestir, detallando las formas de las piezas necesarias para cubrir la estructura del cuerpo.' data-aos="fade-down") 
      .col-lg-6
        img.mb-5(src='@/assets/curso/temas/t1/estructura1.svg', alt='La imagen describe el proceso de creacin de prendas de vestir a partir de la estructura del cuerpo humano. De izquierda a derecha, se ven dos figuras humanas estilizadas etiquetadas como "Figura 3D" o "Estructura". Luego, estas figuras se traducen en un "Plano 2D", que descompone la forma tridimensional en una vista bidimensional. A continuacin, se presentan los "Moldes", que son las plantillas para cortar las piezas de tela necesarias. Finalmente, el proceso culmina en la "Prenda", que es el producto final confeccionado a partir de los moldes, ejemplificado con un saco. Esto ilustra cmo la representacin tridimensional del cuerpo se convierte en una prenda de vestir a travs de un proceso de diseo y confeccin.' data-aos="fade-down") 
    

    .subt.mb-4(data-aos="fade-right")
