        h3.mb-0.ms-2 Procedimientos del patronaje
    p.mb-4(data-aos="fade") Este proceso est compuesto por los siguientes pasos:
    
    AcordionA.t2-1.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul" data-aos="fade-down")
      .row.justify-content-center.align-items-center(titulo="Paso 1: construccin de bsicos")
        .col-md-3.col-8.mb-4.mb-md-0
          img(src='@/assets/curso/temas/t1/acordion.svg', alt='')        
        .col-md-8.mb-4.mb-md-0
          p Los bsicos se dividen en dos grupos: superior (bsico superior + Manga) e inferior, bsico de falda (alrededor de cadera desde cintura hasta rodilla), bsico de pantaln (alrededor de cadera, y la base genital, desde cintura hasta el tobillo).
          p Los bsicos son moldes anatmicos del cuerpo, es decir no son una prenda, son el punto de partida para hacerla. El bsico superior en metodologa SENA es un solo bsico superior, es decir, cualquier tipo de prenda superior. En otras metodologas hay varios bsicos: un solo tipo de prenda, bsico de blusa/camisa, bsico de chaleco, bsico de chaqueta, bsico de abrigo, bsico de vestido, entre otros. 
        
      .row.justify-content-center.align-items-center(titulo="Paso 2: definicin del diseo")
        .col-md-8.mb-4.mb-md-0
          p Por lo general la definicin del diseo no est a cargo del patronista, sin embargo, es la base para definir que se va a desarrollar, es decir, poder hacer el anlisis del tipo de prenda a patronar.
        .col-md-3.col-8
          img(src='@/assets/curso/temas/t1/acordion2.svg', alt='')      
      .row.justify-content-center.align-items-center(titulo="Paso 3: anlisis de la prenda (talla, bsico, horma, material e insumos)")
        .col-md-3.col-8
          img(src='@/assets/curso/temas/t1/acordion3.png', alt='')        
        .col-md-8.mb-4.mb-md-0
          p Se analizan los factores necesarios para el desarrollo de la prenda, despus de los bsicos, son el punto de partida para dar inicio al trazo del plano.
          .row
            .col-lg-9
              .tarjeta.color-primario.p-3
                .row.justify-content-around.align-items-center
                  .col-3.col-sm-2.col-lg-2
