                          span Descargar
                          i.fas.fa-file-download           
      .row.justify-content-center.align-items-center(titulo="Paso 4: construccin de planos")
        .col-md-4.mb-4
          img(src='@/assets/curso/temas/t1/acordion4.svg', alt='La imagen expone tres figuras humanas que representan los planos anatmicos del cuerpo. A la izquierda, el "Plano Frontal" divide el cuerpo en partes anterior y posterior. En el centro, el "Plano Sagital" (marcado como "Plano") divide el cuerpo en mitades derecha ("Der.") e izquierda ("Izq."). A la derecha, el "Plano Transversal" divide el cuerpo en partes superior ("Superior") e inferior ("Inferior"). Estos planos son fundamentales en la anatoma para describir ubicaciones y movimientos en el cuerpo humano.')        
        .col-md-7.mb-4
          p Segn las caractersticas de la prenda y sus componentes los bsicos deben organizarse para facilitar la comprensin de la prenda y simplificar su construccin. La recomendacin de la metodologa SENA es trabajar planos unidos. Estos se clasifican en:
          ul.lista-ul--color.fa-ul.color-primario
            li.mb-1 
              i.fas.fa-plus
              | Plano separado.
            li.mb-1 
              i.fas.fa-plus
