            li.mb-1 
              i.fas.fa-plus
              | Plano abierto delantero o posterior.
            li.mb-1 
              i.fas.fa-plus
              | Plano montado (centro frente, sobre centro atrs).
            li 
              i.fas.fa-plus
              | Plano con unin de hombros.
          
        .bg11.p-5.mb-5
