              h5 Ajuste al plano
              p Desahogos: son aumentos que se dan en algunas medidas de construccin para cumplir con los requerimientos de biomecnica del cuerpo de acuerdo con el tipo y uso de la prenda.
              p.mb-4 Tambin conocidos como holgura, amplitud, flojo. Son medidas que se adicionan al bsico para la construccin del plano, interpretacin o desarrollo de la prenda. Los desahogos permiten la funcionalidad de las prendas sobre prendas.
