      h2 1.5 Elaboracin de bsicos infantil

    p.mb-4(data-aos="fade") Este proceso est diseado para iniciar siempre desde los bsicos en la metodologa SENA, desarrollado de la siguiente forma:

    SlyderF.mb-5(columnas="col-lg-4 col-md-6" data-aos="fade-down")
      .tarjeta-avatar
        img(src='@/assets/curso/temas/t1/bombillo.svg' alt='')
        .tarjeta.bg10.text-white
          .p-4
            h5.text-center Tipo de plano
            p  Identificar qu tipo de plano se va a trabajar, si es un plano unido, separado o montado.      
      .tarjeta-avatar
        img(src='@/assets/curso/temas/t1/bombillo.svg' alt='')
        .tarjeta.bg10.text-white
          .p-4
            h5.text-center Rectngulo
            p Todo bsico inicia siempre desde un rectngulo, el cual se traza con la medida de contorno ms protuberante y el largo ms largo de la parte del cuerpo a trabajar.
      .tarjeta-avatar
        img(src='@/assets/curso/temas/t1/bombillo.svg' alt='')
        .tarjeta.bg10.text-white
          .p-4
            h5.text-center Medida ms larga a trabajar
            p El largo inicial del rectngulo se trabaja con la medida ms larga de la parte del cuerpo a trabajar, por ejemplo, en el pantaln es: largo de pantaln, en la falda: altura de rodilla, en el superior: largo de talle posterior.
      .tarjeta-avatar
        img(src='@/assets/curso/temas/t1/bombillo.svg' alt='')
        .tarjeta.bg10.text-white
          .p-4
            h5.text-center Medida en el trazo
            p Las medidas que se trabajan en la metodologa en algunos casos nicamente se sacan del cuadro de talla, la medida de contorno y largo, no se trabajan medidas como hombro, anchos, cuello y dems. Dichas medidas salen por frmula o como se trabaja en temas anteriores por medidas deducidas.
            p.mb-0 En infantil la medida de cuello posterior equivale a:
            p.mb-0 B/20+2.5 = B (Contorno de pecho) / 20 + 2.5
      .tarjeta-avatar
        img(src='@/assets/curso/temas/t1/bombillo.svg' alt='')
        .tarjeta.bg10.text-white 
          .p-4
            h5.text-center Manejo del papel
            p Por lo general el trazo de moldes se suele hacer con papel trazo o #[em manifold], dicho papel tiene dos caras, una cara que al tacto es liso y visualmente brillante; otra que al tacto es corrugada y visualmente opaca. Por lo general se recomienda trabajar por la parte corrugada con el fin de tener una mejor adhesin del grafito (lpiz) y de no permitir que rebote la luz, es recomendable trabajar en papel blanco con el fin de no cansar la vista y descansar la mente.
      .tarjeta-avatar
        img(src='@/assets/curso/temas/t1/bombillo.svg' alt='')
        .tarjeta.bg10.text-white
          .p-4
            h5.text-center Medida ms protuberante a trabajar
            p Por lo general el manual trabaja con frmulas o convenciones, en la mayora de los casos la medida ms protuberante es sustituida por una letra, por ejemplo, en infantil, contorno de pecho (medida ms protuberante de la cintura para arriba) equivale o es igual a la letra B.
            p.mb-0 B = Contorno de pecho de la talla a trabajar.
            p.mb-0 Talla 6: B = 68 cm (Contorno de pecho del cuadro de tallas correspondiente a la talla 6).

