    p.mb-4(data-aos="fade") Cmo se referencia en temas anteriores, se debe iniciar revisando y analizando el cuadro de tallas para identificar: la talla, las medidas, incrementos y diferencias que se van a trabajar. El desarrollo de bsicos funciona de la siguiente forma:
    
    ul.lista-ul--color.fa-ul.color-primario.ps-2.ps-md-4.mb-5(data-aos="fade")
      li 
        i.fas.fa-plus
        span.mb-0 #[b Tipo de plano:] identificar qu tipo de plano se va a trabajar, si es un plano unido, separado o montado.
      li 
        i.fas.fa-plus
        span.mb-0 #[b Rectngulo:] todo bsico inicia siempre desde un rectngulo, el cual se traza con la medida de contorno ms protuberante y el largo ms largo de la parte del cuerpo a trabajar.
      li 
        i.fas.fa-plus
        span.mb-0 #[b Cuadro de tallas:] en el caso de femenino, para la elaboracin de los bsicos se trabaja con las medidas del cuadro de tallas, es decir cada punto de referencia sobre el cuerpo es trabajado del cuadro de tallas, a diferencia de lo anterior, este desarrollo hace necesario identificar todo el listado de las medidas que se van a implementar para hacer el desarrollo.
      li 
        i.fas.fa-plus
        span.mb-0 #[b Manejo del papel:] por lo general el trazo de moldes se suele hacer con papel trazo o #[em manifold], dicho papel tiene dos caras, una cara que al tacto es liso y visualmente brillante y otra que al tacto es corrugada y visualmente opaca, por lo general se recomienda trabajar por la parte corrugada con el fin de tener una mejor adhesin del grafito (lpiz) y de no permitir que rebote la luz, es recomendable trabajar en papel blanco con el fin de no cansar la vista y descansar la mente.
    
